import {CircularProgress, Grid} from "@mui/material";
import {useEffect, useRef, useState} from "react";
import queryStr from "query-string";
import IFrame from "./compoment/IFrame";
import useGetAdsBuffered from "./hook/useGetAdsBuffered";
import {getDeviceId} from "./utils/ss_device_utils";
import useGetCampaignConfig from "./hook/useGetCampaginConfig";
import useWindowDimensions from "./hook/useWindowDimensions";

const queryString = window.location.search;
const query = queryStr.parse(queryString)
console.log(queryString);
const token = query?.token || undefined
// const screenID = getDeviceId();  // '73B78511F'; // || '73A40551'// || 72820794; 73B78511
const screenID = '73B78511F'; // || '73A40551'// || 72820794; 73B78511
// const screenID = 'Test_Office_Screen_1'
const startTimeStamp = new Date().getTime();


const MainBuffered = () => {
    const [isPlaying, setIsPlaying] = useState(false);
    const [callCount, setCallCount] = useState(0);
    const [errorCount, setErrorCount] = useState(0);
    const {cIsLoading, campaignId, initialImageUrl, duration} = useGetCampaignConfig(token);
    const current = Math.ceil((new Date().getTime() - startTimeStamp) / 1000);
    console.log(`current =>${current}`);
    console.log(`window.devicePixelRatio =>${window.devicePixelRatio}`);
    const remainDuration = duration - current;


    const {
        isLoading,
        currentAds,
        failCount,
        pullNextAds,
        createObjectURL,
        revokeObjectURL,
        trackingSuccess,
        trackingFail,
        updateFailCount,
        // killPlayListReq,
        skipAds,
        // reborn,
        error
    } = useGetAdsBuffered(callCount, screenID, token, remainDuration, campaignId, isPlaying);

    const [blobUrl, setBlobUrl] = useState(null);
    const videoRef = useRef();
    const imgRef = useRef();

    console.log(`isPlaying => ${isPlaying}, remainDuration => ${remainDuration}`)
    if (isPlaying && remainDuration < 10) {
        // killPlayListReq(screenID, token, reborn);
        skipAds();
    }

    // data handler
    useEffect(() => {
        if (currentAds && currentAds.adsType && currentAds.adsType.includes('video') && videoRef.current) {
            if (currentAds.blob) {
                console.log('set blob to video player')
                currentAds.blob.arrayBuffer().then((result) => {
                    const blobRes = createObjectURL(new Blob([result]));
                    console.log(blobRes)
                    setBlobUrl(blobRes);
                    videoRef.current.src = blobRes;
                });
                // const blobRes = createObjectURL(currentAds.blob);
                // console.log(blobRes)
                // setBlobUrl(blobRes);
                // videoRef.current.src = blobRes;
            } else {
                console.log("blob is null");
                updateFailCount();
            }
        }

        setIsPlaying(currentAds != null && currentAds.adsType != null);

    }, [currentAds])

    const {width, height} = useWindowDimensions();

    //pull next ads handler
    useEffect(() => {
        console.log(currentAds);
        if (!currentAds || (!currentAds.nextReqTime && currentAds.nextReqTime === 0)) {
            console.log('not init timer');
            return;
        }
        console.log(`init timer: timeStamp => ${currentAds?.timeStamp}, duration => ${currentAds?.duration}`)

        let timer1 = setTimeout(() => {
            console.log(`update callCount @ nextReqTime after : ${currentAds?.nextReqTime} ms`)
            const c = callCount + 1;
            setCallCount(c);
        }, currentAds?.nextReqTime);

        return () => {
            console.log('clearTimeout nextReqTime')
            clearTimeout(timer1);
        };
    }, [currentAds])

    // request next ads handler
    useEffect(() => {
        if (!currentAds || (!currentAds.duration && currentAds.duration === 0)) {
            console.log('not init timer');
            if (isPlaying) {
                // killPlayListReq(screenID, token, reborn);
                skipAds();
            }
            return;
        }

        let timer1 = setTimeout(() => {
            pullNextAds();
            console.log(`pullNextAds from buffer after ${currentAds?.duration} ms`)
        }, currentAds?.duration);

        return () => {
            console.log('clearTimeout duration')
            clearTimeout(timer1);
        };
    }, [currentAds])

    // fail retry handler
    useEffect(() => {
        let timer1 = setTimeout(() => {
            console.log(`isLoading =>${isLoading} , failCount => ${failCount}`)
            // if (isLoading === false && failCount > 0) {
            if (failCount > 0) {
                console.log('update callCount by failCount @ setTimeout')
                const c = callCount + 1;
                setCallCount(c);
            }
        }, 2000);

        return () => {
            console.log('clearTimeout failCount')
            clearTimeout(timer1);
        };
    }, [failCount])

    // const skipAds = () => {
    //     console.log('skip add, prepare request next')
    //     const c = callCount + 1;
    //     setCallCount(c);
    // }

    return <>
        <div>
            {/*<div style={{*/}
            {/*    zIndex: 101,*/}
            {/*    color: "red",*/}
            {/*    right: 10,*/}
            {/*    top: 10,*/}
            {/*    backgroundColor: "white",*/}
            {/*    fontWeight: 'w400',*/}
            {/*    position: 'fixed'*/}
            {/*}}>*/}
            {/*    /!*error count : {errorCount}*!/*/}
            {/*    screenId: {screenID} , error: {`${error?.name}: ${JSON.stringify(error?.toJSON())}`}*/}
            {/*</div>*/}

            {/*<div style={{*/}
            {/*    top: 0,*/}
            {/*    bottom: 0,*/}
            {/*    left: 0,*/}
            {/*    right: 0,*/}
            {/*    display: 'flex',*/}
            {/*    position: 'fixed',*/}
            {/*    backgroundColor: '#663399',*/}
            {/*    color: 'white',*/}
            {/*    // width: '100%',*/}
            {/*    // height: '100%',*/}
            {/*    justifyContent: 'center',*/}
            {/*    alignItems: 'center',*/}
            {/*}}>*/}
            {/*    <CircularProgress color="inherit" size={100}/>*/}
            {/*</div>*/}

            {
                initialImageUrl ? <img src={initialImageUrl} style={{
                    display: isLoading === false ? 'none' : 'block',
                    zIndex: 100, position: 'fixed'
                }} width='100%' height="100%"/> : <></>
            }


            {/* ads are images */}
            {currentAds && currentAds.adsType?.includes('image') ?
                <img
                    ref={imgRef}
                    src={currentAds.adsUrl}
                    style={{
                        display: currentAds.adsType?.includes('image') ? 'block' : 'none',
                        zIndex: 102, position: 'fixed'
                    }} width='100%' height="100%"/> : <></>
            }

            {/* ads are html */}
            {
                currentAds && currentAds.adsType?.includes('html') ?
                    <IFrame
                        style={{
                            display: currentAds.adsType?.includes('html') ? 'block' : 'none',
                            // zIndex: 100,
                        }}
                        url={currentAds.adsUrl}
                        onLoad={() => {
                            console.log('onload')
                        }}>

                    </IFrame> : <></>
            }

            <div className="video_wrap" style={{
                display: currentAds && currentAds.adsType?.includes('video') ? 'block' : 'none',
                // zIndex: 100,
            }}>
                <video className="blob_video" ref={videoRef}
                       // width={width}
                       // height={(width * 16 / 9)}
                       autoPlay
                       poster="null"
                       loop={currentAds?.loop || false}
                       onPlay={(event) => {
                           console.log('onPlay')
                           // if (currentAds) {
                           //     trackingSuccess(currentAds.trackSuccessUrl, currentAds.supplierId, token, currentAds.adsType);
                           // }
                       }}
                       onPlaying={(event) => {
                           console.log('onPlaying')
                       }}
                       onEnded={(event) => {
                           console.log('onEnded')
                       }}
                       onError={(event) => {
                           console.log("can not play")
                           console.log(event)
                           if (currentAds?.adsType?.includes('video') && event.target.error) {
                               console.log(currentAds);
                               trackingFail(currentAds.trackFailUrl, currentAds.supplier, currentAds.adsType);
                               revokeObjectURL(blobUrl)
                               setBlobUrl(null);
                               setCallCount(0);
                               const errorC = errorCount + 1;
                               setErrorCount(errorC);
                           }
                           // postError(event.target.error);
                           // console.log(`source = ${event.target.src}`);
                           //
                           // switch (event.target.error.code) {
                           //     case event.target.error.MEDIA_ERR_ABORTED:
                           //         alert('You aborted the video playback.');
                           //         break;
                           //     case event.target.error.MEDIA_ERR_NETWORK:
                           //         alert('A network error caused the video download to fail part-way.');
                           //         break;
                           //     case event.target.error.MEDIA_ERR_DECODE:
                           //         alert('The video playback was aborted due to a corruption problem or because the video used features your browser did not support.');
                           //         break;
                           //     case event.target.error.MEDIA_ERR_SRC_NOT_SUPPORTED:
                           //         alert('The video could not be loaded, either because the server or network failed or because the format is not supported.');
                           //         break;
                           //     default:
                           //         alert('An unknown error occurred.');
                           //         break;
                           // }
                       }}
                ></video>
            </div>
        </div>
    </>
};

export default MainBuffered;

