class AdsContent {
    constructor(adsUrl, adsType, duration, timeStamp, trackSuccess, trackFail, blob, loop, supplierId, hive_creativeid) {
        this.adsUrl = adsUrl;
        this.adsType = adsType;
        this.duration = duration;
        this.nextReqTime = (duration - (5 * 1000));
        this.timeStamp = timeStamp;
        this.trackSuccessUrl = trackSuccess;
        this.trackFailUrl = trackFail;
        this.blob = blob;
        this.loop = loop || false;
        this.supplierId = supplierId;
        this.hive_creativeid = hive_creativeid;
    }

    // releaseBlobUrl() {
    //     console.log('releaseBlobUrl')
    //     return (window.URL) ? window.URL.revokeObjectURL(this.adsUrl) : window.webkitURL.revokeObjectURL(this.adsUrl);
    // }
}

export default AdsContent;
