export const postError = async (error) => {
    const response = await myAxios.post(`${process.env.REACT_APP_API_URL}/v1/api/hive/errortracking/`, {
        data: JSON.stringify(error)
    });
    return response;
}

export const getProgrammaticAdsByToken = async (token) => {
    const response = await myAxios.get(`${process.env.REACT_APP_API_URL}/v1/api/programmaticads/${token}`);
    return response;
}

