let db = null;
const _dbName = "ss_video_cache";
const _tableName = "video_cache";
const _dbVersion = 1;
let request = null;

export const initDb = () => {
    console.log('initDb');
    request = window.indexedDB.open(_dbName, _dbVersion);
    request.onerror = (event) => {
        console.error(`Database error: ${event.target.errorCode}`);
    };
    request.onsuccess = (event) => {
        db = event.target.result;
    };

    request.onupgradeneeded = function (event) {
        console.log('onupgradeneeded')
        db = event.target.result;
        let objectStore;
        if (!db.objectStoreNames.contains(_tableName)) {
            objectStore = db.createObjectStore(_tableName, {keyPath: 'vid'});
            objectStore.createIndex('blob', 'blob', {unique: false});
        }
    }
}

export const saveBlobVideo = (vid, blob) => {
    if (db) {
        const objectStore = db.transaction(_tableName, "readwrite").objectStore(_tableName)
        const request = objectStore.add({vid: vid, blob: blob});
        request.addEventListener('success', () => console.log('Record addition attempt finished'));
        request.addEventListener('error', () => console.error(request.error));
    }
}

export const getBlobVideo = (vid) => {
    return new Promise((resolve, reject) => {
        if (db) {
            const objectStore = db.transaction(_tableName).objectStore(_tableName);
            const request = objectStore.get(vid);
            request.addEventListener('success', () => {
                // If the result exists in the database (is not undefined)
                if (request.result) {
                    console.log('taking videos from IDB');
                    // return result if record exist
                    resolve(request.result.blob);
                } else {
                    resolve(null);
                    // return null than fetch the videos from the network
                }
            });
            request.addEventListener('error', () => {
                console.log('request error');
                resolve(null);
            })
        } else {
            resolve(null);
        }
    });
}
