import {useEffect, useState} from "react";
import axios from "../utils/myAxios"
import {
    fakeAds,
    getAds,
    getTmsToken,
    reportFail,
    reportStart,
    reportSuccess,
    sendKillPlayListReq,
    // sendTmsCheck
} from "../api/api";
import AdsContent from "../model/AdsContent";
import myCache from "../utils/cache";
import {getBlobVideo, saveBlobVideo} from "../utils/IdbHelper";
import {skipPlayBack, sleepSync} from "../utils/ss_device_utils";

const cacheKey = "0";
const houseDomain = process.env.REACT_APP_S3_DOMAIN;

const useGetAdsBuffered = (call, screenID, token, remainDuration, campaignId, isPlaying) => {

    const [isLoading, setIsLoading] = useState(true);
    const [failCount, setFailCount] = useState(null);
    const [currentAds, setCurrentAds] = useState(null);
    // const [reborn, setReborn] = useState(true);
    const [error, setError] = useState(null);

    function createObjectURL(object) {
        console.log('createObjectURL')
        return (window.URL) ? window.URL.createObjectURL(object) : window.webkitURL.createObjectURL(object);
    }

    function revokeObjectURL(object) {
        console.log('revokeObjectURL')
        return (window.URL) ? window.URL.revokeObjectURL(object) : window.webkitURL.revokeObjectURL(object);
    }

    useEffect(() => {
            console.log(`call => ${call}, isPlaying =>${isPlaying}`)
            /**
             * fetch advertisement
             * @returns {Promise<void>}
             */
            const fetchAds = async () => {
                console.log('fetchAds')
                let playListReborn;
                try {
                    // check tms still valid
                    // const tmsCheckRes = await sendTmsCheck(screenID, token)
                    // if (tmsCheckRes.status >= 200 && tmsCheckRes.status < 400) {
                    //     playListReborn = tmsCheckRes.data.data.status;
                    //     setReborn(playListReborn)
                    //     if (!playListReborn) {
                    //         await killPlayListReq(screenID, token, playListReborn);
                    //         return;
                    //     }
                    // }
                    // get ads
                    reportStart(screenID, undefined, token);
                    const response = await getAds(screenID, token, remainDuration, campaignId);
                    // const response = await fakeAds();
                    console.log(response.data);
                    if (response.status >= 200 && response.status < 400) {
                        const data = response.data;
                        if (data && Object.keys(data).length === 0
                            && Object.getPrototypeOf(data) === Object.prototype) {
                            // fail
                            console.log('no ads')
                            // const fc = failCount + 1;
                            // setFailCount(fc);
                            if (!isPlaying) {
                                // await killPlayListReq(screenID, token, playListReborn);
                                skipPlayBack();
                            }
                        } else {
                            console.log('parse vast success');
                            /// add random offset to prevent value not change (reactive program value not change not fire event)
                            const duration = (data.duration * 1000) + Math.floor(Math.random() * 180);
                            const url = data.text;
                            const type = data.type;
                            const timestamp = data.time_stamp;
                            console.log(`url => ${url}`);
                            console.log(`type => ${type}`);
                            const supplier = data.supplier_id;
                            const impression = data.impression;
                            const error = data.error;
                            console.log(`impression => ${impression}`);
                            console.log(`error => ${error}`);
                            setFailCount(0);
                            const hive_creativeid = data.hive_creativeid;
                            // if (impression) {
                            let ad;
                            if (type) {
                                if (type.includes('html')) {
                                    // html is non blobs
                                    // const result = await loadHtml(url, impression);
                                    // result ? await trackingSuccess(impression) : await trackingFail(error);
                                    await trackingSuccess(impression, supplier, token, type)
                                    ad = new AdsContent(url, type, duration, timestamp, impression, error, null, false, supplier, hive_creativeid);
                                } else if (type.includes('image')) {
                                    await trackingSuccess(impression, supplier, token, type);
                                    ad = new AdsContent(url, type, duration, timestamp, impression, error, null, false, supplier, hive_creativeid);
                                } else {
                                    console.log(currentAds)
                                    // const lastCreativeId = currentAds?.hive_creativeid || null;
                                    // console.log(`lastCreativeId => ${lastCreativeId} , hive_creativeid => ${hive_creativeid}`)
                                    // if (lastCreativeId == null || lastCreativeId !== hive_creativeid) {
                                    //     // blob download
                                    //     console.log('download video asset')
                                    //     const isHouseVideo = url.startsWith(houseDomain);
                                    //     const blob = await getBlobUrl(url, type);
                                    //     if (!blob) {
                                    //         console.log('blob is null, mark as fail')
                                    //         await trackingFail(error, supplier, type);
                                    //         updateFailCount();
                                    //         return;
                                    //     }
                                    //     ad = new AdsContent(url, type, duration, timestamp, impression, error, blob, isHouseVideo, supplier, hive_creativeid);
                                    // } else {
                                    //     console.log('hive_creativeid is same skip download video asset')
                                    //     ad = new AdsContent(url, type, duration, timestamp, impression, error, currentAds.blob, currentAds.loop, supplier, hive_creativeid);
                                    // }
                                    const isHouseVideo = url.startsWith(houseDomain);
                                    let blob = await getBlobVideo(hive_creativeid);
                                    if (!blob) {
                                        blob = await getBlobUrl(url, type);
                                        if (!blob) {
                                            console.log('blob is null, mark as fail')
                                            await trackingFail(error, supplier, token, type);
                                            updateFailCount();
                                            return;
                                        }
                                        saveBlobVideo(hive_creativeid, blob);
                                    }
                                    await trackingSuccess(impression, supplier, token, type)
                                    ad = new AdsContent(url, type, duration, timestamp, impression, error, blob, isHouseVideo, supplier, hive_creativeid);
                                }
                                // } else {
                                //     // cannot detect ads type make this as fail
                                //     // const fc = failCount + 1;
                                //     // setFailCount(fc);
                                //     if (!isPlaying) {
                                //         await killPlayListReq(screenID, token, playListReborn);
                                //     }
                                //     return;
                                // }
                                //first time
                                if (call === 0 || currentAds === null) {
                                    setIsLoading(false);
                                    setCurrentAds(ad);
                                } else {
                                    addToBuffer(ad);
                                }
                            } else {
                                // no ads
                                if (!isPlaying) {
                                    // await killPlayListReq(screenID, token, playListReborn);
                                    skipPlayBack();
                                }
                            }
                        }
                    } else {
                        console.log('api request fail')
                        setIsLoading(true)
                        // const fc = failCount + 1;
                        // setFailCount(fc);
                        if (!isPlaying) {
                            // await killPlayListReq(screenID, token, playListReborn);
                            skipPlayBack();
                        }

                    }
                } catch (e) {
                    setIsLoading(true)
                    // const fc = failCount + 1;
                    // setFailCount(fc);
                    // setError(e);
                    skipPlayBack();
                }
            }

            const getBlobUrl = async (url, adsType) => {
                try {
                    console.log(url)
                    console.log('getBlobUrl')
                    console.log(`url =>${url} , adsType => ${adsType}`)
                    const config = {
                        responseType: 'blob',
                        // headers: {"Access-Control-Allow-Origin": "*", 'x-amz-acl': 'public-read'}
                    };
                    const response = await axios.get(url, config);
                    let videoUrl = null;
                    if (response.status >= 200 && response.status < 400) {
                        videoUrl = new Blob([response.data]);
                    }
                    return videoUrl;
                } catch (e) {
                    return null;
                }
            };

            /**
             * check html is it available for show
             * @param url
             * @returns {Promise<boolean>}
             */
            // const loadHtml = async (url) => {
            //     try {
            //         console.log('loadHtml')
            //         const response = await axios.get(url);
            //         return response.status >= 200 && response.status < 400
            //     } catch (e) {
            //         return false;
            //     }
            // }

            /**
             * add ads to buffer ( cache )
             * @param ads
             */
            const addToBuffer = (ads) => {
                console.log('addToBuffer()')
                console.log(ads)
                myCache.del(cacheKey);
                myCache.set(cacheKey, ads, 90 * 1000);
            }
            if (token && campaignId) {
                console.log(`token => ${token}, campaignId =>${campaignId}`)
                fetchAds();
            }

        }, [call, screenID, token, campaignId]
    )

    /**
     * pull next ads already buffered at memory
     */
    const pullNextAds = () => {
        console.log('pullNextAds')
        const data = myCache.get(cacheKey);
        console.log(data);
        if (currentAds && data) {
            // if (currentAds.trackSuccessUrl === data.trackSuccessUrl) {
            //     console.log('buffer is old data, mark as fail')
            //     // data.timeStamp = data.timeStamp + 10;
            //     updateFailCount();
            //     setCurrentAds(null)
            //     setIsLoading(true);
            // } else {
            //     console.log('buffer is new, set to current')
            //     setCurrentAds(data);
            // }
            console.log('pull buffer to current')
            setCurrentAds(data);
            myCache.del(cacheKey);
        } else {
            console.log('buffer is null, mark as fail')
            updateFailCount();
            setCurrentAds(null)
            setIsLoading(true);
        }
    }

    const trackingFail = async (errorPostBack, supplier, slug, type) => {
        console.log('trackingFail')
        // if (errorPostBack) {
        //     try {
        //         console.log(`errorPostBack => ${errorPostBack}`)
        //         await axios.get(errorPostBack);
        //     } catch (e) {
        //         console.log(e)
        //         setError(e);
        //     }
        // }
        try {
            await fetch(errorPostBack);
        } catch (e) {
            console.log(e)
            setError(e);
        }

        try {
            await reportFail(screenID, supplier, slug, type);
        } catch (e) {
            console.log(e)
        }
    }

    const trackingSuccess = async (successPostBack, supplier, slug, type) => {
        console.log('trackingSuccess')
        // if (successPostBack) {
        //     try {
        //         console.log(`successPostBack => ${successPostBack}`)
        //         await axios.get(successPostBack);
        //     } catch (e) {
        //         console.log(e)
        //         setError(e);
        //     }
        // }
        try {
            await fetch(successPostBack);
        } catch (e) {
            console.log(e)
            setError(e);
        }

        try {
            await reportSuccess(screenID, supplier, slug, type);
        } catch (e) {
            console.log(e)
        }
    }

    const updateFailCount = () => {
        const fc = failCount + 1;
        setFailCount(fc);
    }

    // const clearAds = () => {
    //     setCurrentAds(null);
    // }

    // const killPlayListReq = async (screenID, token, reborn) => {
    //     console.log('killPlayListReq')
    //     // console.trace()
    //     const response = await getTmsToken(screenID);
    //     if (response.status === 200) {
    //         const password = response.data.data.password
    //         console.log(password);
    //         sendKillPlayListReq(screenID, token, password, reborn)
    //     }
    // }

    const skipAds = () => {
        console.log("call skip from ui")
        skipPlayBack();
        sleepSync(2);
    }

    return {
        isLoading,
        currentAds,
        failCount,
        pullNextAds,
        createObjectURL,
        revokeObjectURL,
        trackingSuccess,
        trackingFail,
        updateFailCount,
        // killPlayListReq,
        skipAds,
        // reborn,
        error
    }

}

export default useGetAdsBuffered;
