export const getDeviceId = () => {
    const mode = process.env.REACT_APP_MODE;
    if (mode === "staging") {
        return 'TESTSCREEN_Street_smart_2';
    }

    let clientInfo;
    try {
        clientInfo = window.client.getClientInfo();
        if (typeof clientInfo === "string") {
            clientInfo = JSON.parse(clientInfo);
        }
    } catch (e) {
        console.log(e);
    }
    // hack for production
    if (clientInfo?.sn === '73A40551' && mode !== "staging") {
        return 'Test_Office_Screen_1';
    }
    return`${clientInfo?.sn}F` ;
}

