import {Grid} from "@mui/material";
import {useEffect, useRef, useState} from "react";
import IFrame from "./compoment/IFrame";
import useGetAds from "./hook/useGetAds";
import queryStr from "query-string";
import {getDeviceId} from "./utils/ss_device_utils";
import useGetCampaignConfig from "./hook/useGetCampaginConfig";

// const screenID = getDeviceId();
const startTimeStamp = new Date().getTime();

const queryString = window.location.search;
const query = queryStr.parse(queryString)
console.log(queryString);
const token = query?.token || undefined
const screenID = getDeviceId();

const Main = () => {
    const config = useGetCampaignConfig(token);
    console.log(`config.duration =>${config.duration}`);

    const current = Math.ceil((new Date().getTime() - startTimeStamp) / 1000);
    console.log(`current =>${current}`);

    const [callCount, setCallCount] = useState(0);
    const {
        isLoading,
        adsUrl,
        adsType,
        duration,
        timeStamp,
        failCount,
        trackingSuccess,
        trackingFail
    } = useGetAds(callCount, screenID, token, config.duration - current);


    const [ready, setReady] = useState(false);
    const [playing, setPlaying] = useState(false);
    const videoRef = useRef();
    const imgRef = useRef();

    useEffect(() => {
        if (adsType && adsType.includes('video') && adsUrl && videoRef.current) {
            videoRef.current.src = adsUrl;
        }
        // if (adsType && adsType.includes('image') && adsUrl && videoRef.current) {
        //     imgRef.current.src = adsUrl;
        // }
    }, [isLoading])

    // useEffect(() => {
    //     let timer1 = setTimeout(() => {
    //         console.log('setTimeout from adsUrl')
    //         if (!adsUrl  && !isLoading) {
    //             const c = callCount + 1;
    //             setCallCount(c);
    //         }
    //     }, 2000);
    //
    //     return () => {
    //         clearTimeout(timer1);
    //     };
    //
    // }, [adsUrl, adsType])

    useEffect(() => {
        console.log(`init timer: timeStamp => ${timeStamp}, duration => ${duration}`)
        if (!duration && duration === 0) {
            console.log('not init timer');
            return;
        }
        let timer1 = setTimeout(() => {
            console.log(`update callCount @ duration after : ${duration}`)
            const c = callCount + 1;
            setCallCount(c);
        }, duration);

        return () => {
            console.log('clearTimeout duration')
            clearTimeout(timer1);
        };
    }, [duration])

    useEffect(() => {
        let timer1 = setTimeout(() => {
            console.log(`isLoading =>${isLoading} , failCount => ${failCount}`)
            if (failCount > 0) {
                console.log('update callCount @ setTimeout')
                const c = callCount + 1;
                setCallCount(c);
            }
        }, 2000);

        return () => {
            console.log('clearTimeout callCount')
            clearTimeout(timer1);
        };
    }, [failCount])

    return <>
        <Grid container>
            <img src={config?.initialImageUrl} style={{
                display: isLoading ? 'block' :"none",
                zIndex: 100, position: 'fixed'
            }} width='100%' height="100%"/>


            {/* ads are images */}
            {
                adsType && adsType?.includes('image') ?
                    <img
                        ref={imgRef}
                        src={adsUrl}
                        style={{
                            display: adsType?.includes('image') ? 'block' : 'none',
                            zIndex: 102, position: 'fixed'
                        }} width='100%' height="100%"/> : <></>
            }

            {/* ads are html */}
            {
                adsType && adsType?.includes('html') ?
                    <IFrame
                        style={{
                            display: adsType.includes('html') ? 'block' : 'none',
                            zIndex: 100,
                        }}
                        url={adsUrl}
                        onLoad={() => {
                            console.log('onload')
                        }}>

                    </IFrame> : <></>
            }

            <div className="video_wrap" style={{
                display: adsType?.includes('video') ? 'block' : 'none',
                // zIndex: 100,
            }}>
                <video className="blob_video" ref={videoRef} autoPlay
                       poster="null"
                       // muted
                    // style={{
                    //     display: adsType?.includes('video') ? 'block' : 'none',
                    //     // zIndex: 100,
                    // }}
                       onPlay={(event) => {
                           console.log('onPlay')
                           trackingSuccess();
                       }}
                       onPlaying={(event) => {
                           console.log('onPlaying')
                           setPlaying(true);
                       }}
                       onEnded={(event) => {
                           console.log('onEnded');
                       }}
                       onError={(event) => {
                           // trackingFail();
                           if (adsType?.includes('video') && event.target.error) {
                               trackingFail();
                           }
                       }}
                ></video>
            </div>
        </Grid>
    </>
};

export default Main;

