import {useEffect, useState} from "react";
import myAxios from "../utils/myAxios";
import {getProgrammaticAdsByToken} from "../api/api";
//
// function get_url_extension(url) {
//     return url.split(/[#?]/)[0].split('.').pop().trim();
// }

const useGetCampaignConfig = (token) => {
    const [isLoading, setIsLoading] = useState(true);
    const [initialImageUrl, setInitialImageUrl] = useState('/assets/SS_Media_Full_Screen.jpg');
    const [campaignId, setCampaignId] = useState(null);
    // const [placeholderUrl, setPlaceholderUrl] = useState(null);
    // const [placeholderType, setPlaceholderType] = useState();
    const [duration, setDuration] = useState(120);
    // const [supplier, setSupplier] = useState();

    const fetchCampaignConfig = async () => {
        setIsLoading(true)
        try {
            const response = await getProgrammaticAdsByToken(token)
            if (response.status >= 200 && response.status < 400) {
                const data = response.data.data[0];
                console.log(data)
                if(data.initial_image) {
                    setInitialImageUrl(data.initial_image);
                }
                // setPlaceholderUrl(data.placeholder);
                // setPlaceholderType(get_url_extension(data.placeholder));
                setDuration(data.duration);
                setIsLoading(false)
                // setSupplier(data.supplier);
                setCampaignId(data.id);
            }
        } catch (e) {
            console.log(e)
            setIsLoading(false)
        }
    }

    useEffect(() => {
        // if (token) {
            fetchCampaignConfig()
        // } else {
        //     setIsLoading(false)
        // }
    }, [])

    return {
        isLoading,
        campaignId,
        initialImageUrl,
        duration,
        // supplier,
        // placeholderType,
        // placeholderUrl
    }
}

export default useGetCampaignConfig;
